.popup {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  min-width: 320px;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  background-color: rgba(0 0 0 / 50%);
  opacity: 0;
  transition: .5s linear;
  visibility: hidden;
}
